<template>
     <div class="row gy-2 pb-3">
        <!-- <div class="col-md-12 mt-2">
            <h3>Información de Víctima</h3>
            <AlertVue :mensajesAlert="mensajesAlert" :alert="tipoAlert"></AlertVue>
        </div>                     --> 
        <slot name="header"/>
        <div class="col-md-2">
            <label class=" fw-semibold d-block" :for="id+'-fechaNacimiento'"> Fecha Nacimiento</label>
            <div class="col-md-12">
                <input :readonly="showVer ?? false" :disabled = "loading" 
                    :value="persona.fechaNacimiento"
