                placeholder="Desconocida" />
            </div>
        </div>
        <div class="col-md-2">
            <label class=" fw-semibold d-block" :for="id+'-dui'"> Dui</label>
            <div class="col-md-12">
                <input :readonly="showVer ?? false" @blur="verificarCasosRelacionados" 
                :value="persona.dui"
                @input="(event) => updateCampo(event, 'dui')"
                type="text" class="form-control" :id="id+'-dui'" placeholder="99999999-9" 
                :disabled="loading || getEdadInt(persona.fechaNacimiento)<18"/>
            </div>
        </div>
        <FormDeptoMuniVue 
            :showVer="showVer ?? false"
            :persona="'Residencia'"
            :departamentos="(opciones===null?[]:opciones.departamentos)"
            :departamento="persona.departamento" 
            :municipio="persona.municipio" 
            @update:departamento ="value=>updateCampoValue(value, 'departamento')"
            @update:municipio ="value=>updateCampoValue(value, 'municipio')"
