<template>   
    <div class="col-md-3">
        <label class=" fw-semibold d-block" :for="'caso-primer-nombre-'+persona"><code>*</code> Primer Nombre</label>
        <div class="col-md">
            <input :disabled="loading??false" :readonly="showVer ?? false" :value="primerNombre" @input="updatePrimerNombre" type="text" class="form-control" :id="'caso-primer-nombre-'+persona" placeholder="Ingrese" />
        </div>
    </div>
    <div class="col-md-3">
        <label class=" fw-semibold d-block" :for="'caso-segundo-nombre-'+persona"> Segundo Nombre</label>
        <div class="col-md">
            <input :disabled="loading??false" :readonly="showVer ?? false" :value="segundoNombre" @input="updateSegundoNombre" type="text" class="form-control" :id="'caso-segundo-nombre-'+persona" placeholder="Ingrese" />
        </div>
    </div>
    <div class="col-md-3">
        <label class=" fw-semibold d-block" :for="'caso-primer-apellido-'+persona"><code>*</code> Primer Apellido</label>
        <div class="col-md">
            <input :disabled="loading??false" :readonly="showVer ?? false" :value="primerApellido" @input="updatePrimerApellido" type="text" class="form-control" :id="'caso-primer-apellido-'+persona" placeholder="Ingrese" />
        </div>
    </div>
    <div class="col-md-3">
        <label class=" fw-semibold d-block" :for="'caso-segundo-apellido-'+persona"> Segundo Apellido</label>
        <div class="col-md">
            <input :disabled="loading??false" :readonly="showVer ?? false" :value="segundoApellido" @input="updateSegundoApellido" type="text" class="form-control" :id="'caso-segundo-apellido-'+persona" placeholder="Ingrese" />
        </div>
    </div>    
</template>
<script>
export default{
    props:['persona','primerNombre', 'segundoNombre', 'primerApellido', 'segundoApellido', 'showVer', 'loading'],
    setup(props,{emit}) {
        const updatePrimerNombre = (event) => {
            emit('update:primerNombre', event.target.value);
        };
        const updateSegundoNombre = (event) => {
            emit('update:segundoNombre', event.target.value);
        };
        const updatePrimerApellido = (event) => {
            emit('update:primerApellido', event.target.value);
        };
        const updateSegundoApellido = (event) => {
            emit('update:segundoApellido', event.target.value);
        };

        return{
            updatePrimerNombre,
            updateSegundoNombre,
            updatePrimerApellido,
            updateSegundoApellido
        }
    }
}
</script>